import React from 'react';
import SVG from './SVG'

export default function Card({style}) {
    return (
        <div
            className="pricing-item features-item ja-animate pricing__item--featured"
            data-animation="move-from-bottom"
            data-delay="item-1"
            style={{ minHeight: 497 }}
        >
            <SVG style={style} />
            <ul className="pricing-feature-list">
                <li className="pricing-feature">10 Searches</li>
                <li className="pricing-feature">In-Call Checks</li>
                <li className="pricing-feature">On Message/SMS Checks</li>
            </ul>
            <button className="pricing-action">Choose plan</button>
        </div>
    );
}
